.g--card-5{
    @include make-card-5(
        $title-font: c,
        $subtitle-font: f,
        $link-class: g--link-1,
    );
    @include make-card-5-color(
        $background-color: map-get($color-options, f),
        $title-color: map-get($color-options, a),
        $subtitle-color: map-get($color-options, a),
    );
}