@mixin make-col($i) {
  // $value: (100% / $columns)*$i;
  $value: 0.6944444444444% * ($columns * $i);
  flex: 0 0 $value;
  max-width: $value;
  position: relative;
  min-height: 1px;
  padding-right: $half-gutter-width;
  padding-left: $half-gutter-width;
  transition: $time-b;
}

/* Start Responsive Offset */
@mixin make-offset($i) {
  // $value: (100% / $columns)*$i;
  $value: 0.6944444444444% * ($columns * $i);
  margin-left: $value;
  transition: $time-b;
}

/* Start Responsive Order */
@mixin make-order($i) {
  order: $i;
}